'use client';

import React from 'react';
import Link from 'next/link';
import Image from 'next/image';

export default function AuthLayout({ children }: { children: React.ReactNode; }) {
    
    return (
        <div className="min-h-screen flex flex-col">
            <header className="bg-slate-800 text-white py-4 px-6 shadow-md">
                <div className="container mx-auto flex items-center justify-between">
                    <Link href="/" className="flex items-center gap-2">
                        <h1 className="text-xl font-bold">Cine Conecta</h1>
                    </Link>
                    <nav>
                        <ul className="flex space-x-6">
                            <li>
                                <Link href="/auth/login" className="hover:text-slate-300 transition">
                                    Iniciar Sesión
                                </Link>
                            </li>
                            <li>
                                <Link href="/auth/register" className="hover:text-slate-300 transition">
                                    Registrarse
                                </Link>
                            </li>
                        </ul>
                    </nav>
                </div>
            </header>

            <main className="flex-grow container mx-auto py-6 px-4">
                {children}
            </main>
            
            <footer className="bg-slate-800 text-white py-3 text-center">
                <p>© {new Date().getFullYear()} CineConecta. Todos los derechos reservados.</p>
            </footer>
        </div>
    );
}